.extra-nav {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style-type: none;
    color: @light-text;
}

.extra-nav__item {
    margin-right: 49px;
    padding-top: 11px;
    padding-bottom: 12px;

    &:last-child {
        margin-right: 0;
    }
}

.extra-nav__link {
    line-height: 20px;
    text-transform: capitalize;
    color: @light-text;
    transition: 0.25s ease-out all;

    &--active {
        color: #FFD91D 
    }

    &:hover {
        color: #FFD91D
    }

    &:active {
        color: darken(#FFD91D, 13%);
    }
}