.banner {
    margin-bottom: 40px;
    text-align: center;
    background: linear-gradient(to top,#b8544a 90%, #fff 50%);
    background-image: url("../img/top-banner.png");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: contain;
}

.banner_wrap {
    display: inline-block;
    margin-left: 160px;
    padding-top: 29px;
    padding-bottom: 15px;
}

.banner__text {
    margin: 0;
    margin-bottom: 3px;
    font-size: 24px;
    font-weight: bold;
    line-height: 36px;
    color: @white;
}

.banner__link {
    display: inline-block;
    padding: 8px 80px;
    font-weight: bold;
    font-size: 18px;
    line-height: 27px;
    background: linear-gradient(180deg, #5CD000 0%, #32870A 100%);
    border-radius: 6px;
    text-transform: uppercase;
    color: @white;
    cursor: pointer;
    transition: 0.35s ease-in-out all;

    &:hover {
        background: linear-gradient(180deg, #32870A 0%, #5CD000 100%);
    }

    &:active {
        background: linear-gradient(180deg, darken(#32870A, 5%) 0%, darken(#5CD000, 5%) 100%);
    }
}