.games {
    margin-bottom: 36px;
}

.games__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style-type: none;
    color: @white;
}

.games__item {
    position: relative;
    width: 209px;
    margin-bottom: 24px;
    padding-top: 3px;
    padding-left: 3px;
    padding-right: 3px;
    background: linear-gradient(233.61deg, #0078D7 3.33%, #005597 94.86%);;
    border-radius: 6px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
    text-align: center;
    box-sizing: border-box;

    &:hover > .games__action {
        display: block;
    }

    &:nth-last-child(-n+5) {
        margin-bottom: 0;
    }
}

.games_picture {
    margin-bottom: 2px;
    border-radius: 6px;
}

.games__title {
    margin: 0;
    margin-bottom: 6px;
    font-size: 13px;
    font-weight: normal;
    line-height: 16px;
}

.games__top {
    position: absolute;
    top: -5px;
    left: -6px;
    width: 85px;
    height: 74px;
    overflow: hidden;
    text-indent: -1000px;
    background: url(../img/top-game.png) no-repeat 0 0;
    z-index: 20;
}

.games__action {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 6px;
}