@font-face {
  font-family: "Livvic";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Livvic-Regular.woff2") format("woff2"), url("../fonts/Livvic-Regular.woff") format("woff");
}
@font-face {
  font-family: "Livvic";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Livvic-Bold.woff2") format("woff2"), url("../fonts/Livvic-Bold.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../fonts/Rubik-Regular.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Rubik-Bold.woff2") format("woff2"), url("../fonts/Rubik-Bold.woff") format("woff");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-weight: normal;
  line-height: 135%;
  font-family: "Livvic", "Arial", sans-serif;
  color: #737373;
  background-color: #010207;
  background-image: url("../img/bg-image.jpg");
  background-repeat: repeat-x;
  background-attachment: fixed;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.mobile-container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
}
.tablet-container {
  width: 708px;
  padding: 0 30px;
}
.container {
  width: 1140px;
  margin: 0 auto;
  padding: 0 55px;
}
.container--main {
  padding-top: 28px;
  background-color: #fff;
}
.page-header__top {
  display: flex;
  align-items: center;
  padding-top: 9px;
  padding-bottom: 7px;
  background-color: #001627;
}
.page-header__logo {
  margin-left: 89px;
  margin-right: 222px;
}
.page-header__bottom {
  background-color: #0078D7;
}
@media (max-width: 1560px) {
  .page-header__top {
    justify-content: space-between;
  }
  .page-header__logo {
    margin-left: 50px;
    margin-right: 0;
  }
}
.main-nav__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #EEEEEE;
}
.main-nav__item {
  margin-right: 48px;
  padding-top: 22px;
  padding-bottom: 22px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  line-height: 20px;
  text-transform: uppercase;
  color: #EEEEEE;
  transition: 0.25s ease-out all;
}
.main-nav__link--active {
  color: #FFD91D;
}
.main-nav__link:hover {
  color: #FFD91D;
}
.main-nav__link:active {
  color: #dab500;
}
@media (max-width: 1560px) {
  .main-nav {
    margin-right: 50px;
  }
}
.extra-nav {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #EEEEEE;
}
.extra-nav__item {
  margin-right: 49px;
  padding-top: 11px;
  padding-bottom: 12px;
}
.extra-nav__item:last-child {
  margin-right: 0;
}
.extra-nav__link {
  line-height: 20px;
  text-transform: capitalize;
  color: #EEEEEE;
  transition: 0.25s ease-out all;
}
.extra-nav__link--active {
  color: #FFD91D;
}
.extra-nav__link:hover {
  color: #FFD91D;
}
.extra-nav__link:active {
  color: #dab500;
}
.banner {
  margin-bottom: 40px;
  text-align: center;
  background: linear-gradient(to top, #b8544a 90%, #fff 50%);
  background-image: url("../img/top-banner.png");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
}
.banner_wrap {
  display: inline-block;
  margin-left: 160px;
  padding-top: 29px;
  padding-bottom: 15px;
}
.banner__text {
  margin: 0;
  margin-bottom: 3px;
  font-size: 24px;
  font-weight: bold;
  line-height: 36px;
  color: #ffffff;
}
.banner__link {
  display: inline-block;
  padding: 8px 80px;
  font-weight: bold;
  font-size: 18px;
  line-height: 27px;
  background: linear-gradient(180deg, #5CD000 0%, #32870A 100%);
  border-radius: 6px;
  text-transform: uppercase;
  color: #ffffff;
  cursor: pointer;
  transition: 0.35s ease-in-out all;
}
.banner__link:hover {
  background: linear-gradient(180deg, #32870A 0%, #5CD000 100%);
}
.banner__link:active {
  background: linear-gradient(180deg, #296f08 0%, #51b700 100%);
}
.content {
  margin-bottom: 40px;
}
.content--mb {
  margin-bottom: 54px;
}
.content--mb-last {
  margin-bottom: 70px;
}
.content h1 {
  margin: 0;
  margin-bottom: 13px;
  font-size: 36px;
  font-weight: bold;
  line-height: 45px;
  color: #001627;
}
.content h2 {
  margin: 0;
  margin-bottom: 13px;
  font-size: 24px;
  font-weight: bold;
  line-height: 30px;
  color: #001627;
}
.content p {
  margin: 0;
}
.games {
  margin-bottom: 36px;
}
.games__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #ffffff;
}
.games__item {
  position: relative;
  width: 209px;
  margin-bottom: 24px;
  padding-top: 3px;
  padding-left: 3px;
  padding-right: 3px;
  background: linear-gradient(233.61deg, #0078D7 3.33%, #005597 94.86%);
  border-radius: 6px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
  text-align: center;
  box-sizing: border-box;
}
.games__item:hover > .games__action {
  display: block;
}
.games__item:nth-last-child(-n+5) {
  margin-bottom: 0;
}
.games_picture {
  margin-bottom: 2px;
  border-radius: 6px;
}
.games__title {
  margin: 0;
  margin-bottom: 6px;
  font-size: 13px;
  font-weight: normal;
  line-height: 16px;
}
.games__top {
  position: absolute;
  top: -5px;
  left: -6px;
  width: 85px;
  height: 74px;
  overflow: hidden;
  text-indent: -1000px;
  background: url(../img/top-game.png) no-repeat 0 0;
  z-index: 20;
}
.games__action {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 6px;
}
.columns {
  display: flex;
  justify-content: space-between;
}
.columns__content {
  width: 750px;
}
.columns__baner {
  width: 360px;
}
.button {
  font-size: 18px;
  font-weight: bold;
  font-family: "Rubik";
  line-height: 21px;
  text-transform: uppercase;
  border-radius: 47px;
  color: #fff;
  cursor: pointer;
}
.button--blue {
  position: absolute;
  top: 33%;
  left: 50%;
  margin-left: -69px;
  padding: 13px 22px;
  background: linear-gradient(180deg, #015597 0%, #0178D7 100%);
}
.button--blue:hover {
  background: linear-gradient(180deg, #0178D7 0%, #015597 100%);
}
.button--blue:active {
  background: linear-gradient(180deg, #016abe 0%, #01477e 100%);
}
.button--orange {
  padding: 13px 35px;
  background: linear-gradient(180deg, #FFCA00 0%, #FF9600 99.5%);
  border-radius: 35px;
}
.button--orange:hover {
  background: linear-gradient(180deg, #FF9600 0%, #FFCA00 100%);
}
.button--orange:active {
  background: linear-gradient(180deg, #e68700 0%, #e6b600 100%);
}
.review {
  width: 100%;
  margin-bottom: 42px;
  border-collapse: separate;
  border-spacing: 0;
  border: 5px solid #0078D7;
  border-radius: 6px;
}
.review__head {
  line-height: 20px;
  font-variant: small-caps;
  background-color: #0078D7;
  color: #ffffff;
}
.review__head th {
  padding-left: 11px;
  padding-right: 11px;
  padding-top: 9px;
  padding-bottom: 16px;
  font-weight: normal;
}
.review__casino {
  width: 344px;
  text-align-last: left;
}
.review__data {
  font-family: "Rubik";
  border-radius: 6px;
  background-color: #fff;
}
.review__link {
  text-decoration: underline;
  color: #001627;
}
.review__link:hover {
  color: #000000;
}
.review__link:active {
  opacity: 0.5;
}
.review__data td {
  position: relative;
  padding-top: 10px;
  padding-bottom: 8px;
  text-align: center;
}
.review__data td::after {
  content: "";
  position: absolute;
  right: 0;
  top: 12px;
  width: 1px;
  height: 55px;
  background-color: #ECECEC;
}
.review__data td:last-child::after {
  display: none;
}
.review__casino-value {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 19px;
  padding-right: 15px;
}
.review_number {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 33px;
  font-size: 14px;
  font-weight: bold;
  line-height: 17px;
  text-align: center;
  text-transform: uppercase;
  background-image: url("../img/heart.svg");
  background-position: center;
  background-repeat: no-repeat;
  color: #ffffff;
}
.review__bonus-value {
  font-size: 24px;
  font-weight: bold;
  line-height: 28px;
  color: #001627;
}
.sidebar__block {
  margin-bottom: 22px;
  border-radius: 5px;
}
.sidebar__block:last-child {
  margin-bottom: 0;
}
.sidebar__block--slot {
  width: 360px;
  height: 256px;
  margin-bottom: 30px;
  padding-left: 9px;
  padding-right: 9px;
  padding-bottom: 10px;
  padding-top: 11px;
  background: linear-gradient(232deg, #0178D7 3.33%, #015597 94.86%);
  border-radius: 6px;
  box-sizing: border-box;
}
.sidebar__block-top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 7px;
}
.sidebar__block-top::before {
  content: "";
  position: absolute;
  top: -7px;
  left: 9px;
  width: 62px;
  height: 62px;
  background-image: url("../img/medal.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  z-index: 10;
}
.sidebar__block-title {
  padding-left: 69px;
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
  color: #ffffff;
}
.sidebar__block-best-slot {
  border-radius: 6px;
}
.sidebar-wrap {
  position: relative;
  height: 87%;
}
.sidebar-wrap:hover > .games__action {
  display: block;
}
.sidebar__block--casino {
  display: flex;
  flex-direction: column;
  width: 360px;
  height: 331px;
  padding-left: 13px;
  padding-right: 13px;
  padding-top: 10px;
  background-color: #0078D7;
  background-image: url("../img/aside-bottom-banner.jpg");
  background-position: bottom;
  background-repeat: no-repeat;
  border-radius: 6px;
  box-sizing: border-box;
  text-align: center;
}
.sidebar__block--casino .sidebar__block-top::before {
  top: 2px;
  left: 18px;
  width: 18px;
  height: 18px;
  background-image: url("../img/cup.svg");
}
.sidebar__block--casino .sidebar__block-title {
  padding-left: 47px;
}
.sidebar__block--casino .button {
  margin-top: auto;
  margin-bottom: 39px;
  align-self: center;
}
.page-footer {
  position: relative;
  display: flex;
  padding-top: 42px;
  padding-bottom: 21px;
  background-color: #002D4F;
}
.page-footer::before {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 335px;
  height: 262px;
  background-image: url("../../img/footer-image.png");
}
.page-footer__logo {
  margin-left: 89px;
  margin-right: 136px;
}
.page-footer .main-nav__list {
  margin-bottom: 19px;
  padding-bottom: 10px;
  border-bottom: 1px solid #194261;
}
.page-footer .main-nav__item {
  padding-top: 0;
}
.page-footer__copyright {
  text-align: center;
}
.page-footer__copyright-text {
  line-height: 20px;
  color: #4A7393;
}
@media (max-width: 1560px) {
  .page-footer {
    flex-direction: column;
    align-items: center;
  }
  .page-footer::before {
    display: none;
  }
  .page-footer__logo {
    margin-right: 0;
    margin-left: 0;
    margin-bottom: 30px;
  }
}
