.review {
    width: 100%;
    margin-bottom: 42px;
    border-collapse: separate; 
    border-spacing: 0;
    border: 5px solid #0078D7;
    border-radius: 6px;
}

.review__head {
    line-height: 20px;
    font-variant: small-caps;
    background-color: #0078D7;
    color: @white;
}

.review__head th {
    padding-left: 11px;
    padding-right: 11px;
    padding-top: 9px;
    padding-bottom: 16px;
    font-weight: normal;
}

.review__casino {
    width: 344px;
    text-align-last: left;
}

.review__data {
    font-family: "Rubik";
    border-radius: 6px;
    background-color: #fff;
}

.review__link {
    text-decoration: underline;
    color: #001627;

    &:hover {
        color: darken(#001627, 15%)
    }

    &:active {
        opacity: 0.5;
    }
}

.review__data td {
    position: relative;
    padding-top: 10px;
    padding-bottom: 8px;
    text-align: center;

    &::after {
        content: "";
        position: absolute;
        right: 0;
        top: 12px;
        width: 1px;
        height: 55px;
        background-color: #ECECEC;
    }

    &:last-child::after {
        display: none;
    }
}

.review__casino-value {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 19px;
    padding-right: 15px;
}

.review_number {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 36px;
    height: 33px;
    font-size: 14px;
    font-weight: bold;
    line-height: 17px;
    text-align: center;
    text-transform: uppercase;
    background-image: url("../img/heart.svg");
    background-position: center;
    background-repeat: no-repeat;
    color: @white;
}

.review__bonus-value {
    font-size: 24px;
    font-weight: bold;
    line-height: 28px;
    color: #001627;
}