.page-header__top {
    display: flex;
    align-items: center;
    padding-top: 9px;
    padding-bottom: 7px;
    background-color: #001627;
}

.page-header__logo {
    margin-left: 89px;
    margin-right: 222px;
}

.page-header__bottom {
    background-color: #0078D7;
}

@media (max-width: 1560px) {
    .page-header__top {
        justify-content: space-between;
    }
    .page-header__logo {
        margin-left: 50px;
        margin-right: 0;
    }
}