.sidebar__block {
    margin-bottom: 22px;
    border-radius: 5px;

    &:last-child {
        margin-bottom: 0;
    }
}

.sidebar__block--slot {
    width: 360px;
    height: 256px;
    margin-bottom: 30px;
    padding-left: 9px;
    padding-right: 9px;
    padding-bottom: 10px;
    padding-top: 11px;
    background: linear-gradient(232deg, #0178D7 3.33%, #015597 94.86%);
    border-radius: 6px;
    box-sizing: border-box;
}

.sidebar__block-top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 7px;

    &::before {
        content: "";
        position: absolute;
        top: -7px;
        left: 9px;
        width: 62px;
        height: 62px;
        background-image: url("../img/medal.svg");
        background-repeat: no-repeat;
        background-position: 0 0;
        z-index: 10;
    }
}

.sidebar__block-title {
    padding-left: 69px;
    font-size: 20px;
    font-weight: bold;
    line-height: 25px;
    color: @white;
}

.sidebar__block-best-slot {
    border-radius: 6px;
}

.sidebar-wrap {
    position: relative;
    height: 87%;

    &:hover > .games__action {
        display: block;
    }
}

.sidebar__block--casino {
    display: flex;
    flex-direction: column;
    width: 360px;
    height: 331px;
    padding-left: 13px;
    padding-right: 13px;
    padding-top: 10px;
    background-color: #0078D7;
    background-image: url("../img/aside-bottom-banner.jpg");
    background-position: bottom;
    background-repeat: no-repeat;
    border-radius: 6px;
    box-sizing: border-box;
    text-align: center;
}

.sidebar__block--casino .sidebar__block-top {
    &::before {
        top: 2px;
        left: 18px;
        width: 18px;
        height: 18px;
        background-image: url("../img/cup.svg");
    } 
} 

.sidebar__block--casino .sidebar__block-title {
    padding-left: 47px;
}

.sidebar__block--casino .button {
    margin-top: auto;
    margin-bottom: 39px;
    align-self: center;
}