.button {
    font-size: 18px;
    font-weight: bold;
    font-family: "Rubik";
    line-height: 21px;
    text-transform: uppercase;
    border-radius: 47px;
    color: #fff;
    cursor: pointer;

    &--blue {
        position: absolute;
        top: 33%;
        left: 50%;
        margin-left: -69px;
        padding: 13px 22px;
        background: linear-gradient(180deg, #015597 0%, #0178D7 100%);
        
        &:hover {
            background: linear-gradient(180deg, #0178D7 0%, #015597 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#0178D7, 5%) 0%, darken(#015597, 5%) 100%);
        }
    }

    &--orange {
        padding: 13px 35px;
        background: linear-gradient(180deg, #FFCA00 0%, #FF9600 99.5%);
        border-radius: 35px;

        &:hover {
            background: linear-gradient(180deg, #FF9600 0%, #FFCA00 100%);
        }

        &:active {
            background: linear-gradient(180deg, darken(#FF9600, 5%) 0%, darken(#FFCA00, 5%) 100%);
        }
    }
}