.main-nav__list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style-type: none;
    color: @light-text;
}

.main-nav__item {
    margin-right: 48px;
    padding-top: 22px;
    padding-bottom: 22px;

    &:last-child {
        margin-right: 0;
    }
}

.main-nav__link {
    line-height: 20px;
    text-transform: uppercase;
    color: @light-text;
    transition: 0.25s ease-out all;

    &--active {
        color: #FFD91D;
    }

    &:hover {
        color: #FFD91D;
    }

    &:active {
        color: darken(#FFD91D, 13%);
    }
}

@media (max-width: 1560px) {
    .main-nav {
        margin-right: 50px;
    }
}


