.page-footer {
    position: relative;
    display: flex;
    padding-top: 42px;
    padding-bottom: 21px;
    background-color: #002D4F;

    &::before {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 335px;
        height: 262px;
        background-image: url("../../img/footer-image.png");
    }
}

.page-footer__logo {
    margin-left: 89px;
    margin-right: 136px;
}

.page-footer .main-nav__list {
    margin-bottom: 19px;
    padding-bottom: 10px;
    border-bottom: 1px solid #194261;
}

.page-footer .main-nav__item {
    padding-top: 0;
}

.page-footer__copyright {
    text-align: center;
}

.page-footer__copyright-text {
    line-height: 20px;
    color: #4A7393;
}

@media (max-width: 1560px) {
    .page-footer {
    flex-direction: column;
    align-items: center;

        &::before {
            display: none;
        }
    }

    .page-footer__logo {
        margin-right: 0;
        margin-left: 0;
        margin-bottom: 30px;
    }
}