.content {
    margin-bottom: 40px;

    &--mb {
        margin-bottom: 54px;
    }

    &--mb-last {
        margin-bottom: 70px;
    }
}

.content h1 {
    margin: 0;
    margin-bottom: 13px;
    font-size: 36px;
    font-weight: bold;
    line-height: 45px;
    color: @title-text;
}

.content h2 {
    margin: 0;
    margin-bottom: 13px;
    font-size: 24px;
    font-weight: bold;
    line-height: 30px;
    color: @title-text;
}

.content p {
    margin: 0;
}